import React,{useContext} from 'react';
import styled from 'styled-components';
import {Link} from 'react-router-dom';
import { Context } from './../data/contextProvider';
const Project =(props)=>{
  const value=useContext(Context);
  const {id,name,img}=props.project;
return(
    <React.Fragment>
      <ProjectWrapper className="col-9 mx-auto col-md-6 col-lg-4" >
          <div className="project">
          <div className="img-container p-5"
                onClick={()=>{value.handleDetail(id)}}>    
        <Link to="/details">
        <img src={img} alt="" className="card-img-top" />
        </Link>
        </div>
        <div className="card-footer d-flex justify-content-between">
            <p className="align-self-center mb-0" style={{fontFamily:"'Shadows Into Light', cursive",fontSize:"28px",
                                                            color:"#F54BA3"}}>{name}</p>
            </div>
        </div>
      </ProjectWrapper>
    </React.Fragment>
);
}
export default Project

export const ProjectWrapper=styled.div`

.project{
    border-color: transparent;
    transition: width 2s;
    border-raduis:10px
}
.card-footer {
  background: transparent;
  border-top: transparent;
  transition: all 1s linear;
}
&:hover {
  .project {
    border: 2px solid rgba(0, 0, 0, 0.2); ;
    box-shadow: 2px 2px 5px 0px rgba(0, 0, 0, 0.2);;
  }
  .card-footer {
    background:rgba(247, 247, 247);
    font-family:'Shadows Into Light', cursive;
    font-size:26px;
  }
}
  .card-img-top {
    overflow:hidden;
    border-raduis:10px;
    transition: all 1s linear;
    transform: scale(1.2);
    position: relative;
    display:block;
  }
  .img-container {
    position: relative;
    overflow: hidden;
  }
  .card-img-top {
    transition: all 1s linear;
  }
  .img-container:hover .card-img-top {
    transform: scale(1.2);
  }
  .overlay {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: #008CBA;
    overflow: hidden;
    width: 100%;
    height: 0;
    transition: .5s ease;
  }
  
  .container:hover .overlay {
    height: 100%;
  }
`;
