import React from 'react';
import img from './images/IMG_20200512_002829.jpg'
import styled from 'styled-components'
import Projects from './projects'
import Scrollspy from 'react-scrollspy'
import Contact from './contact';
const Home=()=>{
  
  return (
    <React.Fragment>
      <HomeWrapper>
        <section id="home">
        <div className="container control">
          <h1>hello world! </h1>
          <hr ></hr>
      <div className="row">
        <div className="col-sm-4"> 
        <img src={img} alt="" className="rounded-circle"></img>
        </div>
        <div className="col-sm-8"> a girl seeking for creating websites,Bringing mockups to life using HTML, CSS, JavaScript,React
        </div>
      </div>
      </div>
      </section>
      < section id="projects">
        <div className="about">
          <h1>Latest Works</h1>
          <hr></hr>
         <Projects/> 
         </div>
      </section>
      <section id="contact">
        <h1>Contact</h1>
        <hr></hr>
        <Contact/>
      </section>
     
      </HomeWrapper>

    </React.Fragment>
  );

}
export default Home;


export const HomeWrapper=styled.div`
.control{
  justify-content:center;
  height:50%;
  width:80%;
  padding-left:20px;
  text-align:center;
  font-family: 'Shadows Into Light', cursive;
  font-size:32px;
}
h1{
  text-align:center;
  font-family: 'Shadows Into Light', cursive;
  font-size:32px;
}
hr{
  width:100px;
  border:2px solid #F54BA3 ;
  margin-top:0;
}
.rounded-circle{
  width:80%;
  height:80%;
  margin: 28px 28px 0px 28px;
  padding:0px 0px 0px 20px;
  border-radius:10%;
}
`;