import React,{useContext,useState} from 'react'
import {Context} from './../data/contextProvider'
import {Link} from 'react-router-dom';
import styled from 'styled-components'
const ProjectDetail=(props)=>{
  const value=useContext(Context)
  const [isOpen, setIsOpen] = useState(false);
  
  const toggle = () => setIsOpen(!isOpen);

  const {id,name,title,img,link}=value.projectDetail;
  return (
    <DetailWrapper>
     <React.Fragment>
       <h1>Project detail</h1>
       <hr></hr>
       <div className="container py-5">
              {/* title */}
              <div className="row">
            <div className="col-10 mx-auto text-center text-slanted text-blue my-5">
                  <h2 style={{  marginTop:"20px"}}>{name}</h2>
                </div>
              </div>
              <div className="row">
                <div className="col-10 mx-auto col-md-6 my-3">
                  <img src={img} className="img-fluid" alt="" />
                </div>
              </div>
            <div className="row">
            <div className="col-10 mx-auto text-center text-slanted text-blue my-5">
                  <h4 style={{fontFamily:"'Shadows Into Light', cursive"}}> {title} </h4>
                </div>
              </div>
              <div className="row justify-content-center">
          
                <div id="demo" className="a"> <h1>Links </h1>
                  <a href="https://samar-hash.github.io/react-recipe/" target="blank">Recpie application</a> <br/>
                  <a href="https://samar-hash.github.io/react-to-do/" target="blank">to-do application</a><br/>
                  <a href="https://samar-hash.github.io/javascriptgame/" target="blank">Roll-dice game</a><br/>
                  <a href="https://samar-hash.github.io/black-white/" target="blank">furniture design</a><br/>
                  <a href="https://samar-hash.github.io/interior-design/" target="blank">interior design</a>

                </div>
              </div>
      </div>
    </React.Fragment>
  </DetailWrapper>
  );
};
export default ProjectDetail;

export const DetailWrapper=styled.div`
.py-5{
  height:50%;
  width:60%;
  border: 3px solid rgba(247, 247, 247);
}
h1{
  text-align:center;
  font-family: 'Shadows Into Light', cursive;
  font-size:32px;
}
hr{
  width:100px;
  border:2px solid #F54BA3 ;
  margin-top:0;
}
.btn{
  padding:8px;
  margin:12px;
  background:#F54BA3
}
.a{
  font-size:30px
  margin:14px;
}`;