import React,{useState} from 'react';
import './App.css';
import { Route, Switch } from "react-router-dom";
import {ThemeProvider} from 'styled-components';
import {GlobalStyles} from './components/darkmode/global'
import {darkTheme,lightTheme} from './components/darkmode/theme';
import Toggle from './components/darkmode/toggleBtn';

import Navbar from './components/nav/nav';
import Home from './components/containers/Home';
import ProjectDetails from './components/containers/projectDetail'
function App() { 
  const [theme, setTheme] = useState('light');
  const toggleTheme = () => {
    if (theme === 'light') {
      setTheme('dark');
    } else {
      setTheme('light');
    }
  }
  return (
    <ThemeProvider theme={theme === 'light' ? lightTheme : darkTheme} >
      <>
      <GlobalStyles />
      <Navbar />
      <Toggle theme={theme} toggleTheme={toggleTheme} />
      <h4 style={{textAlign:"center",color:"#F54BA3",fontFamily:"'Shadows Into Light',cursive"}}>
         {theme === 'light' ? 'light mode' : 'dark mode'}</h4>
      <Switch>
        <Route path="/" component={Home} exact />
        <Route path="/details" component={ProjectDetails} exact />

      </Switch>
      <div className="footer" style={{textAlign:"center",background:"#F5F5F5",left:"0",
                                        padding:"15px 0px",width:"100%"}}>
           <h5 style={{color:"#F54BA3"}}>Copyright   <span style={{fontFamily:"'Shadows Into Light',cursive",fontSize:"30px"}}> @SAMAR MAHMOUD</span></h5>
            </div>
    </>
    </ThemeProvider>
  );
}

export default App;